
// 26个英文字母的base64数据
const a = 'data:image/png;base64,'
const b = 'data:image/png;base64,'
const c = 'data:image/png;base64,'
const d = 'data:image/png;base64,'
const e = 'data:image/png;base64,'
const f = 'data:image/png;base64,'
const g = 'data:image/png;base64,'
const h = 'data:image/png;base64,'
const i = 'data:image/png;base64,'
const j = 'data:image/png;base64,'
const k = 'data:image/png;base64,'
const l = 'data:image/png;base64,'
const m = 'data:image/png;base64,'
const n = 'data:image/png;base64,'
const o = 'data:image/png;base64,'
const p = 'data:image/png;base64,'
const q = 'data:image/png;base64,'
const r = 'data:image/png;base64,'
const s = 'data:image/png;base64,'
const t = 'data:image/png;base64,'
const u = 'data:image/png;base64,'
const v = 'data:image/png;base64,'
const w = 'data:image/png;base64,'
const x = 'data:image/png;base64,'
const y = 'data:image/png;base64,'
const z = 'data:image/png;base64,'

const wordArr = [
  'CAT',
  'DOG',
  'PIG',
  'TIGER',
  'PANDA',
  'BIRD'
]
const num = 3 // 随机选择单词的数量

const outWord = function () {
  const wordArr = [
    { str: 'A',base64: a},
    { str: 'B',base64: b},
    { str: 'C',base64: c},
    { str: 'D',base64: d},
    { str: 'E',base64: e},
    { str: 'F',base64: f},
    { str: 'G',base64: g},
    { str: 'H',base64: h},
    { str: 'I',base64: i},
    { str: 'J',base64: j},
    { str: 'K',base64: k},
    { str: 'L',base64: l},
    { str: 'M',base64: m},
    { str: 'N',base64: n},
    { str: 'O',base64: o},
    { str: 'P',base64: p},
    { str: 'Q',base64: q},
    { str: 'R',base64: r},
    { str: 'S',base64: s},
    { str: 'T',base64: t},
    { str: 'U',base64: u},
    { str: 'V',base64: v},
    { str: 'W',base64: w},
    { str: 'X',base64: x},
    { str: 'Y',base64: y},
    { str: 'Z',base64: z},
  ]

  return wordArr
}

// 0-单词库数量之间的随机整数
function getRandom () {
  const arr = wordArr.slice()
  return Math.floor(Math.random() * arr.length)
}
// 从单词库中生成不重复的随机单词
function getUnique (arrResult) {
  const arr = wordArr.slice()
  const randomNum = getRandom()

  let word = ''
  if (arrResult.indexOf(arr[randomNum]) >= 0) {
    // 递归调用，避免重复
    return getUnique(arrResult)

  } else {
    word = arr[randomNum]

  }

  return word
}

const randomWords = function () {
  const arrResult = []
  for (let i = 0; i < num; i++) {
    let chooseWord = getUnique(arrResult)

    arrResult.push(chooseWord)
  }

  return arrResult
}



export {
  outWord,
  randomWords
}


